@import "../../../../base";

.mas-video-container {
  box-shadow: none;
  .modal-content {
    background-color: transparent;
    display: flex;
    align-items: center;
  }

  .modal-header {
    border: none;
    align-self: flex-end;

    span {
      color: $color-hover;
      text-shadow: none;
      font-size: $font-size-extra-big;
    }
  }

  iframe {
    max-width: 100%;
  }

  .spinner-animation {
    z-index: -1;

    .dot1,
    .dot2 {
      background-color: $color-hover !important;
    }
  }
}