@import "../../../base";

.landing-mas-container {
  min-height: 700px;
  position: relative;
  background-color: white;
  overflow: hidden;

  @media screen and (min-width: 576px) {
    min-height: 900px;
  }

  @media screen and (min-width: 992px) {
    min-height: 700px;
  }

  @media screen and (max-width: 991px) {
    margin-bottom: 30px;
  }

  .landing-mas-inner-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    margin: auto;
    // max-width: 1063px;

    @media screen and (max-width: 991px) {
      padding: 0 15px;
    }

    .mas-info-container {
      position: relative;
      z-index: 1;
      @media screen and (max-width: 991px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;

        h2 {
          max-width: 450px;
        }
      }

      h2 {
        margin-top: 45px;
        font-weight: $font-weight-heavy;
        font-size: $font-size-extra-tiny * 3;
        color: $color-input-form-landing-desc;

        @media screen and (max-width: 575px) {
          font-size: $font-size-regular;
        }
      }
    }
  
    .mas-video-container {
      display: flex;
      justify-content: center;
      align-items: center;
      
      button {
        transition: transform 0.3s ease;
        &:hover {
          transform: scale(1.1);
        }
      }

      .curved-box {
        max-width: 487px;
        width: 100%;
        height: auto;
        transform: translateY(30px);
      }

      .mas-bg {
        width: 150%;
        max-width: 100vw;
        position: absolute;
        right: 0;
        left: 0;
        // top: 0;
        margin: auto;
        bottom: 0;
        transform: translateX(-21%) translateY(15%);
    
        @media screen and (max-width: 991px) {
          width: 100%;
          transform: translateY(10%) translateY(15%);
        }

        @media screen and (max-width: 575px) {
          width: 150%;
          transform: translateY(10%);
        }
      }
    }
  }
}